.grid {
    display: grid;
    place-items: center;
  }

  .grid , .header{
    display: grid;
    width: 100%;
    place-items: center;
    grid-template-columns: 1fr;
  }
  
  .start-1{
    grid-column-start: 1;
  }
  
  .start{
    place-items: start;
  }
  
  .span {
    grid-column: 1/-1;
  }

  .span-cols{
    grid-column: span 2;
  }
  