.appear {
  animation: appear 300ms ease-out;
}

@keyframes appear {
  0% { transform: scale(0.2); }
100% { transform: scale(1);}
}

.grow {
  animation: grow 1600ms ease-out infinite;
}

@keyframes grow {
  0% { transform: scale(1); }
50% { transform: scale(1.5);}
100% { transform: scale(1);}
}


.shake {
animation: shake 500ms ease-out ;
}

.shake-infinite {
  animation: shake 500ms ease-out infinite ;
  }
  

@keyframes shake {
  0% { transform: rotate(0deg); }
 33% { transform: rotate(2deg);}
 66% { transform: rotate(-1deg); }
100% { transform: rotate(0deg); }
}

.opacity {
  animation: opacity 550ms ease-out ;
  }
  
  @keyframes opacity {
    0% { opacity: 0; }
  100% { opacity: 1;}
  }
  

.slide-up {
  animation: slide-up 400ms linear;
}

@keyframes slide-up {
  0% {
    transform: translateY(15%);
    opacity: (0.5);
  }
  100% {
    transform: translate(0, 0);
    opacity: (1);
  }
}


.slide-in {
  animation: slide 400ms linear;
}


@keyframes slide {
  0% {
    transform: translate(60%, 0%);
    opacity: (0);
  }
  100% {
    transform: translate(0, 0);
    opacity: (1);
  }
}

.spin {
  animation: spin 1s linear infinite;
}


@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(359deg);
  }
}

.glow {
  animation: glow 3s linear infinite;
}

@keyframes glow {
  0% {
    filter: drop-shadow(6px 6px 7px black);
  }
  50% {
    filter: drop-shadow(2px 2px 2px black);
  }
  100% {
    filter: drop-shadow(6px 6px 7px black);
  }
}

