.bold{
    font-weight: bold;
  }
  .txt{
  &-xsm {
    font-size: .7em;
  }

  &-sm {
    font-size : .85em;
  }
  &-md {
    font-size : 1.5em;
  }
  
  &-lgx {
    font-size : 2.7em;
  }
  
 &-lg {
    font-size : 2em;
  }

  &-lgx {
    font-size : 4.5em;
  }
  
  &-xl{
    font-size: 10em;
  }
}

  .txt-left{
    text-align: left !important;
    padding-left: 1em;
    width: 100%;
  }

  .txt-center{
    margin: auto;
    text-align: center;
  }
  
  .txt-right{
    text-align: right !important;
  }

  .txt-left{
    margin: auto;
    padding: 0;
    text-align: left !important;
  }
  .truncate {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .black{
    color: black;
  }
  
  .txt-white{
    color: white;
  }
  
  
.capitalize{
    text-transform:capitalize;
  }
  
  .txt-shadow{
    text-shadow: 1.2px 1.2px 2px black;
  }
  
  @media (max-width : 1350px){
    .txt-xl{
      font-size: 7em;
    }
  }

  @media (max-width : 1250px){
    .txt-xl{
      font-size: 6em;
    }
  }

  @media (max-width : 1150px){
    .txt-xl{
      font-size: 4.5em;
    }
  }

  @media (max-width : 860px){
    .txt-xl{
      font-size: 10em;
    }
  }