.ant-input, .ant-input-affix-wrapper, .ant-picker-focused, .ant-picker{
    padding: .6em;
    border-radius: .6em;
  }

  .ant-popover{
    position: fixed !important;
    border-radius: .5em !important;
  }

  .ant-popover-inner{
      border-radius: .5em !important;
  }

  .min-height-table{
    min-height: 400px;
  }

   .ant-list-item{
    padding: 0 !important;
  }
.vx-tooltip-portal{
  z-index: 10000;
}
#header > .ant-select > .ant-select-selector {
  background: transparent !important;
  color: white !important;
  border: 2px solid white;
}

#header > .ant-select > .ant-select-selector > .ant-select-selection-item {
  background: transparent !important;
}

.ant-select-arrow{
  color: inherit !important;
}