@import "https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.8.2/css/all.min.css";
@import "./text.scss";
@import "./mobile.css";
@import "./animation.css";
@import "./grid.css";
@import "./antd.css";

.txt-overflow{
  text-overflow: ellipsis; 
  height: 1.5em; 
  overflow: hidden; 
  white-space: nowrap;
}
.overflowY {
  overflow-y: scroll;
}
.noanimation {
  animation: none;
}

.noselect {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
}
@mixin transform($property) {
  -webkit-transform: $property;
  -ms-transform: $property;
  transform: $property;
}

.rounded {
  border-radius: 1em;
  background: teal;
  color: white;
  padding: 0.2em 0.4em 0.1em 0.4em;
}

a:hover, a:active{
  color: inherit !important;
}

.rounded-btn {
  border-radius: 50%;
  background: teal;
  color: white;
  padding: 0.2em 0.4em 0.1em 0.4em;
}

.pointer {
  cursor: pointer;
}

.invert {
  background: white !important;
}
.full-height {
  min-height: 100vh;
}
.ant-select-disabled.ant-select-single:not(.ant-select-customize-input)
  .ant-select-selector {
  border-radius: 0.5em;
}
.full-width {
  width: 100%;
  max-width: 100%;
}

.hide {
  display: none;
}

.top-right,
.top-left,
.bottom-left,
.bottom-right,
.top-center,
.bottom-center {
  position: fixed;
  z-index: 1000;
}

.top-right,
.top-left,
.top-center {
  top: 1rem;
}

.bottom-right,
.bottom-left {
  bottom: 1rem;
}

.top-center,
.bottom-center {
  right: calc(50vw - 7rem);
}
.bottom-right,
.top-right {
  right: 1rem;
}

.bottom-left,
.top-left {
  left: 1rem;
}

.round {
  border-radius: 50%;
  height: 12px;
  width: 12px;
  box-shadow: 1px 1px 2px black;
  display: inline-block;
}
.red {
  color: red;
}
.green {
  color: green;
}
.hoverr:hover,
.hover-txt:hover > i {
  cursor: pointer;
  text-shadow: 1px 1px lightgray;
  transform: scale(1.3);
  transition: 200ms ease-out;
}

.badge{
  color: white;
  padding: .1em .5em;
  border-radius: 1em;
  background-color: #33B482;
}



.inverted{
  color: #33B482;
  transition: 200ms ease;
  border: 2px solid #33B482;
  background-color: white;
}

p{
  margin: 0;

}

.inverted:hover{
  color: white;
  background-color: #33B482;
}

.hover-bold:hover {
  font-weight: bolder;
  transition: 200ms ease-out;
}

.active-nav {
  color: white;
}

.activee {
  border: 1px solid;
  padding: 0.1em;

  border-radius: 0.3em;
}

.full {
  height: 100vh;
  width: 100vw;
}

.eventHover:hover {
  background-color: rgb(112, 214, 221);
}
.round-bordered {
  border-radius: 5em;
  border: 2px solid;
}

.disabled{
  background: gray;
  color: white;
}

.pad {
  padding: 0.35em;
}

.shadow {
  box-shadow: 0 2px 4px rgba(10, 30, 10, 0.25), 0 2px 2px rgba(0, 0, 0, 0.22);
}

.shadow-light {
  box-shadow: 0 1px 3px rgba(15, 39, 15, 0.25), 0 2px 2px rgba(0, 0, 0, 0.22);
}


.img-shadow {
  filter: drop-shadow(2px 2px 3px gray);
}

.gray {
  background: gray;
}

.white {
  background: rgb(223, 218, 218);
}

.round-btn {
  color: white;
  border-radius: 50%;
  border: 0;
  font-size: 1em;
  width: 2em;
  height: 2em;
  margin: 0.2em;
}

.round-btn:hover {
  background: red;
}
.scale-hover:hover {
  transition: 200ms ease-out;
  transform: scale(1.2);
}

select {
  border-radius: 20px;
}

.full-col{
  grid-column: 1/-1;
  width: 100% !important;
}

.right:hover,
.left:hover {
  transform: scale(1.5);
}
.right {
  float: right;
}
.left {
  justify-self: start;
}

.hover:hover + .hover-show {
  opacity: 1 !important;
}

.hover {
  cursor: pointer;
}
a.underline:hover {
  transition: 200ms ease-out;
  text-decoration: underline;
}
@media (max-height: 600px) {
  .hide-height {
    display: none;
  }
}
.span-2{
  grid-column: span 2;
}
@media (max-width: 860px) {
  .span-2{
    grid-column: span 1;
  }
  .mobile-panel {
    grid-template-columns: 1fr 1fr !important;
  }
}

@media (max-width: 1360px) {
  .overlay-tablet {
    position: absolute !important;
  }

}

@media (min-width: 860px) {
  .left-on-desktop {
    padding: 0 0 0 70px !important;
  }
}

.wave {
  animation: wave 7s cubic-bezier(0.36, 0.45, 0.63, 0.53) -0.125s infinite,
    swell 7s ease -1.25s infinite;
}
.pad {
  font-size: 1.112em;
}

.warning {
  color: orange;
}

.error {
  color: tomato;
}

.success {
  color: teal;
}

.graph-label-container {
  display: inline-block;
  position: relative;
}

.graph-label1 {
  background-color: rgb(242, 166, 0);
  border-radius: 24px;
  margin: 0px 4px;
  padding: 1px 12px;
  color: rgba(0, 0, 0, 0.87);
  height: 18px;
  line-height: 18px; 
  font-size: .9em;
  font-weight: normal;
}
.noscroll {
  position: fixed;
  overflow: hidden;
}
.graph-label2 {
  background-color: rgb(255, 255, 255);
  box-shadow: rgba(0, 0, 0, 0.2) 0px 2px 4px -1px, rgba(0, 0, 0, 0.14) 0px 4px 5px 0px, rgba(0, 0, 0, 0.12) 0px 1px 10px 0px;
  color: rgb(0, 0, 0);
  font-size: .9em;
  left: 16px;
  line-height: 20px;
  position: absolute;
  text-align: left;
  top: 25px;
  visibility: hidden;
  z-index: 5;
  border-radius: 2px;
  padding: 5px 12px;
  width: 200px;
}
.graph-label1:hover ~ .graph-label2 {
  visibility: visible
}