.txt-sm{
  font-size: .5em;
}
@media (max-width: 1100px){
.hide-tablet{
  display: none !important;
}
}
@media (max-width: 860px){
    .txt-xl{
      font-size: 6em;
    }
    .mobile-margin-top{
      margin-top: 10vh !important;
    }
    .ui.secondary.pointing.menu .item{
      padding: 0.5em;
    }
  
    .hide-mobile{
      display: none !important;
    }
  
    .show-mobile{
      display: grid !important;
    }

    .txt-left-mobile{
      text-align: left;
    }
    .left-mobile{
     float: left ;
     color: gray
    }
    .mobile-width{
      width: 95%;
    }
  }